// Defines the Receipt module (Model, Collection, Views, Router)
define('Receipt', [
    'Receipt.Router',
    'Utils'
], function (Router) {
    'use strict';
    return {
        mountToApp: function (application) {
            return new Router(application);
        }
    };
});